html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }
  html > *:after, html > *:before {
    box-sizing: inherit; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type='checkbox'],
input[type='radio'] {
  box-sizing: border-box;
  padding: 0; }

input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  height: auto; }

input[type='search'] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/*--------------------------------------------------------------
# Embedded Fonts
--------------------------------------------------------------*/
h2,
.entry-title {
  font-family: "Lora", serif; }

.entry-description {
  font-family: "Lato", sans-serif;
  color: #378244; }

p {
  font: 16px "Lato", sans-serif; }

.btn {
  background: #378244;
  color: white;
  border: 0;
  border-radius: 2em;
  padding: 2% 3%;
  font-size: 16px;
  font-family: "Lato", sans-serif; }
  .btn:hover {
    cursor: pointer;
    border: 1px solid #378244;
    color: #378244;
    background: white;
    font-weight: 500; }

.wpcf7-submit {
  color: #378244;
  border-color: 2px solid #378244;
  border-radius: 20px;
  font-family: lora;
  font-size: 16px; }

.wpcf7-form {
  color: #378244;
  font-family: "Lora", serif, serif; }

.wpcf7-form-control {
  color: black;
  border: 1px solid black; }

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

input {
  border: 0; }

.banner {
  width: 100%;
  display: block;
  height: inherit; }
  .banner .banner-img {
    width: auto;
    height: 60vmax; }
    .banner .banner-img img {
      object-fit: cover;
      height: 100%;
      width: 100%; }
  .banner .banner-content {
    background: rgba(111, 143, 106, 0.76);
    position: absolute;
    display: flex;
    width: 45%;
    height: inherit;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 2% 5%;
    top: 0;
    transform: translateY(25rem); }
    .banner .banner-content h2 {
      color: white;
      font-size: 2.7rem;
      line-height: 1.2; }

article {
  width: 100%;
  height: inherit; }

.contact-form-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.contact-page-info {
  border-top: 1px solid black; }

.media-enquiries {
  border-top: 1px solid black; }

@media only screen and (min-width: 601px) {
  .entry-header {
    width: 80%;
    margin: 0 auto;
    display: flex;
    flex-flow: row nowrap; }
    .entry-header nav {
      flex: 1 0 50%;
      margin: 0 auto; }
    .entry-header .entry-title {
      font-size: 2.7rem; }
    .entry-header .entry-description {
      font-size: 16px; }
    .entry-header p {
      line-height: 1.5;
      text-align: justify; }
  .entry-content {
    width: 80%;
    margin: 0 auto; }
    .entry-content nav {
      width: 60%;
      padding: 2% 0;
      text-align: center; }
      .entry-content nav .btn {
        margin: 0 1%;
        width: 30%;
        position: relative;
        display: inline-flex;
        justify-content: center; }
      .entry-content nav ul {
        display: flex;
        flex-flow: row wrap;
        margin-bottom: 5%;
        text-align: left; }
        .entry-content nav ul li {
          flex: 1 0 50%; }
          .entry-content nav ul li h3 {
            font-family: "Lato", sans-serif;
            font-size: 28px;
            color: #e4936a; }
  .testimonials {
    width: 80%;
    margin: 0 auto; } }

.menu li {
  list-style: none; }

.site-info {
  display: flex;
  justify-content: space-around;
  margin: 0 auto; }
  .site-info .hide {
    display: none; }
  .site-info h4 {
    font-family: "Lora", serif; }
  .site-info p.copyright {
    font-family: "Lato", sans-serif;
    font-size: 10px; }
    .site-info p.copyright span {
      text-transform: capitalize; }
